body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#components-layout-demo-fixed .logo {
  float: left;
  width: 120px;
  height: 31px;
  margin: 16px 24px 16px 0;
  background: rgba(251, 251, 251, 0.93);
}
.site-layout {
  /* background: rgb(204, 204, 204); */
  height: calc(100% - 100px);
  background: url(/doorprize-randomizer/static/media/background.2eb615ba.png) no-repeat center center fixed;
  background-size: cover;
}

.ant-layout .ant-layout-header {
  background: #c7c7c7;
}

.numbers {
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  font-size: 110px;
  line-height: 1em;
  text-align: center;
  margin: auto;
  overflow: hidden;
}

.numbers__window {
  display: inline-block;
  overflow: hidden;
  width: 0.6em;
  height: 1em;
}

.numbers__window__digit {
  font: inherit;
  word-break: break-all;
  display: block;
  width: 0;
  padding: 0 0.53em 0 0;
  margin: 0 auto;
  overflow: inherit;
  -webkit-animation: counting 0.6s steps(10) forwards infinite;
          animation: counting 0.6s steps(10) forwards infinite;
}

.numbers__window__digit::before {
  content: attr(data-fake);
  display: inline-block;
  width: 100%;
  height: auto;
}

@-webkit-keyframes counting {
  100% {
    -webkit-transform: translate3d(0, -10em, 0);
            transform: translate3d(0, -10em, 0);
  }
}

@keyframes counting {
  100% {
    -webkit-transform: translate3d(0, -10em, 0);
            transform: translate3d(0, -10em, 0);
  }
}

.numbers__window__digit--0 {
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}
.numbers__window__digit--1 {
  -webkit-animation-iteration-count: 2;
          animation-iteration-count: 2;
}
.numbers__window__digit--2 {
  -webkit-animation-iteration-count: 4;
          animation-iteration-count: 4;
}
.numbers__window__digit--3 {
  -webkit-animation-iteration-count: 6;
          animation-iteration-count: 6;
}
.numbers__window__digit--4 {
  -webkit-animation-iteration-count: 8;
          animation-iteration-count: 8;
}
.numbers__window__digit--5 {
  -webkit-animation-iteration-count: 10;
          animation-iteration-count: 10;
}

.actionIcon {
 cursor: pointer;
}

.ant-select-single.ant-select-show-arrow .ant-select-selection-item, .ant-select-single.ant-select-show-arrow .ant-select-selection-placeholder {
  padding-right: 18px;
  font-weight: 600;
}
